.masthead {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
  justify-content: space-around;
  position: fixed;
  background-color: #fff;
  width: 100%;
  height: 200px;
  -webkit-align-items: center;
  align-items: center;
  background-repeat: no-repeat;
  z-index: 1;
}

.headnav {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  position: fixed;
  width: 100%;
}

